@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{box-sizing:border-box;font-family:'Roboto', sans-serif}body{margin:0;background-color:#fafafa}.emoji{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:center;justify-content:center;margin:16px;border:1px solid #dbdbdb;padding:4px;border-radius:8px;background-color:white;cursor:pointer;transition:-webkit-transform .15s;transition:transform .15s;transition:transform .15s, -webkit-transform .15s}.emoji>img{max-width:64px}.emoji:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.emojiWrapper{position:relative;width:100%;top:100px;display:-webkit-flex;display:flex;-webkit-flex-flow:row wrap;flex-flow:row wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:50px 5vw}.topbar{padding:10px calc(5vw + 20px);height:100px;width:100%;display:-webkit-flex;display:flex;-webkit-flex-flow:row nowrap;flex-flow:row nowrap;-webkit-align-items:center;align-items:center;background-color:rgba(47,72,88,0.9);position:fixed;z-index:1;-webkit-backdrop-filter:blur(8px);backdrop-filter:blur(8px);-webkit-justify-content:center;justify-content:center;box-shadow:0px 2px 2px rgba(0,0,0,0.4)}.search{min-width:Min(100%, 400px);-webkit-flex:0 1;flex:0 1}.search>input{font-size:25px;border:none;border-radius:8px;padding:4px 8px;width:100%}.search>input:active,.search>input:focus{outline:none}

